/* style.css */

/* Container styles */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

/* Heading styles */
h1 {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
}

/* Link styles */
a {
  color: #4f46e5;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Stock list styles */
ul {
  list-style: none;
  padding: 0;
}

li {
  margin-bottom: 10px;
}

/* Stock detail styles */
.stock-detail {
  margin-top: 20px;
}

/* Add stock form styles */
.add-stock-form {
  margin-top: 20px;
}

.add-stock-form input[type="text"] {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
}

.add-stock-form button {
  padding: 8px 16px;
  background-color: #4f46e5;
  color: #fff;
  border: none;
  cursor: pointer;
}

.add-stock-form button:hover {
  background-color: #4338ca;
}
