/* StockList.css */

.stock-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .stock-table th,
  .stock-table td {
    padding: 8px;
    border: 1px solid #ddd;
    text-align: center; /* Align content to the right by default */
  }
  
  .stock-table th:first-child,
  .stock-table td:first-child {
    text-align: left; /* Align the first column (Stock Code) to the left */
  }
  
  .stock-table th {
    background-color: #f2f2f2;
  }
  
  .stock-table tbody tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .stock-table tbody tr:hover {
    background-color: #ddd;
  }
  